                progressBar: false,\n                modal: true,\n                text: \"Confirm Flush ?\",\n                buttons: [\n                    Noty.button(\n                        \"YES\",\n                        \"btn btn-danger\",\n                        function () {\n                            flushRole();\n                        },\n                        { id: \"yes\", \"data-status\": \"ok\" }\n                    ),\n\n                    Noty.button(\"NO\", \"btn btn-error\", function () {\n                        flushConfirmation.close();\n                    }),\n                ],\n            }).show(); //end of delete confirmation\n\n            function flushRole() {\n                axios\n                    .get(`/api/admin/roles/flush`)\n                    .then((response) => {\n                        if (response.status == 200) {\n                            flushConfirmation.close();\n                            new Noty({\n                                layout: \"topRight\",\n                                text: \"Roles Flushed Successfully\",\n                                type: \"success\",\n                                timeout: 2000,\n                            }).show();\n                        }\n                    })\n                    .catch((error) => {\n                        console.log(error);\n                    }); //end of api req\n            } // end of flushrole func\n        },\n    },\n};\n</script>\n\n<style scoped>\n@media screen and (max-width: 1633px) {\n    .flush {\n        margin-top: 5px;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
